<template>
    <div class="musician">
        <div class="music_nav">
            <h3 class="nav_h3">音乐人top</h3>
            <div @click="toggleIan(index),toggleSong(item.id)" :class="{fontColor:fontc==index}" class="nav_ian" v-for="(item,index) in artists" :key="index">{{item.name}}<span>/{{item.alias[0]}}</span></div>
        </div>
        <div class="music_song">
            <h3 class="song_h3">最新专辑</h3>
            <hr style="width:90%;margin:0px auto;border:0.5px solid #D9D9D9">
            <div class="song_con">
                <div class="song_c" v-for="(item,index) in hotAlbums" :key="index">
                    <img class="c_img" :src="item.picUrl">
                    <div class="c_strat">
                        <router-link :to="{path:'/album',query:{id:item.id}}">
                            <span class="iconfont icon-bofang1"></span>
                        </router-link>
                        
                    </div>
                    <p class="c_p">{{item.name}}</p>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            artists:[],
            hotAlbums:[],
            fontc:null,
        }
    },
    methods: {
        useraxios(httpUrl){
            return new Promise((resolve,reject)=>{
                 axios.post(httpUrl).then((res)=>{
                    resolve(res.data)
                })
                .catch((res)=>{
                    console.log(res)
                })
            })
            
        },
        toggleIan:function(index){
            this.fontc=index
        },
        toggleSong:function(id){
            this.useraxios(`http://musicapi.leanapp.cn/artist/album?id=${id}&limit=10`)
            .then((data)=>{
                this.hotAlbums=data.hotAlbums;   
            });
        }
    },
    beforeMount() {
        this.useraxios('http://musicapi.leanapp.cn/artist/list?limit=20')
        .then((data)=>{
            this.artists=data.artists
        });
        this.useraxios(`http://musicapi.leanapp.cn/artist/album?id=${this.$route.params.id}&limit=10`)
        .then((data)=>{
            this.hotAlbums=data.hotAlbums;   
        });
    },
}
</script>

<style lang="less">
@mainColor:#00B9F2;
.musician{
    display: flex;
    .music_nav{
        width: 200px;
        background: rgb(243,243,243);
        .nav_h3{
            width: 70%;
            margin: 50px auto 30px;
            text-align: left;
            font-family: "楷体";
            font-weight: 700;
            font-size: 26px;
            color: #333;
        }
        .nav_ian{
            .nav_h3;
            margin: 10px auto ;
            height: 30px;
            line-height: 30px;
            font-size: 16px;
            font-weight: normal;
            border-bottom: 1px solid #E7E7E7;
            span{
                color:#999 ;
                font-size: 14px;
            }
        }
        .nav_ian:hover{
            text-decoration-line:underline;
            cursor: pointer;
        }
    }
}
.music_song{
    width: 80%;
}
.song_h3{
    text-align: left;
    margin: 50px 0px 0px 50px;
    color: @mainColor;
    font-size: 24px;
    text-shadow: 0px 0px 2px;
}
.song_con{
    margin: 50px ;
    display: flex;
    flex-wrap: wrap;
    .song_c{
        width: 170px;
        margin:8px;
        position: relative;
        .c_p{
            text-align: center;
            font-size: 14px;
            color: #333;
            font-weight: 700;
        }
    }
}
.c_strat{
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            cursor: pointer;
            span{
                width: 30px;
                display: block;
                margin: 130px 0px 0px 130px;
                font-size: 30px;
                color: #FFF;
            }
            span:hover{
                color: @mainColor;
            }
        }
.c_img{
    width: 170px;
    height: 170px;
            
}
.fontColor{
    color: @mainColor !important;
}
</style>